body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container h1{
  border-bottom: 1px solid rgba(224, 224, 224, 0.774);
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
}

.container .row {
  margin-bottom: 15px;
}

.TaskForm .input-form{
  margin-bottom: 10px;
}

.TaskForm .button-form{
  display: flex;
  justify-content: center;
}

.TaskForm .mr-auto{
  width: 100%;
}

.TaskForm .delete-icon {
  width: 15px;
  float: right;
  margin-top: 3px;
  cursor: pointer;
}

.TaskList {
  text-align: center;
}

.TaskList .none-center{
  text-align: left;
}

.check-icon {
  width: 15px;
  margin-left: 10px;
  margin-bottom: 5px;
}
